import React from "react";
import "../styles/Videoplayer.css"
import YouTube from "react-youtube";


const Videoplayer = ({currTrack, videoId, handleNextTrack }) => {
  if (!videoId) {
    return (
      <p style={{ textAlign: "center", fontSize: "18px", fontWeight: "bold" }}>
        Waiting on video
      </p>
    );
  }

  // Play next track after current one is finished playing
  const checkElapsedTime = (e) => {
    if (e.target.playerInfo.playerState=== 0) {
      handleNextTrack()
    }
  };

  // variable options
  // In our case, autoplay when loaded
  const opts = {
    playerVars: {
      autoplay: 1
    }
  };


  return (
    <React.Fragment>
    <div className="video-player">
      <YouTube
        videoId={videoId}
        containerClassName="embed embed-youtube"
        onStateChange={(e) => checkElapsedTime(e)}
        opts={opts}
      />
      
    </div>
    {currTrack.snippet.title !== undefined && 
      <div className="video-player-title">
      Currently Playing: {currTrack.snippet.title}
      </div>
    }
    </React.Fragment>
  );
};

export default Videoplayer;