import React from 'react';
import { Button, Card } from 'react-bootstrap';
import _ from 'lodash'
import defaultLogo from '../images/default-cover-art.png';
import '../styles/PlaylistList.css'
const PlayList = ({ playlists, handlePlaylistSelect, loadMore, type }) => {

  const handleSelect = (data) => {
    handlePlaylistSelect(data)
  }

  const handleLoadClick = () => {
    loadMore(playlists, type)
  }
  return (
    <div className="PlaylistList">
      <div className="PlaylistList-items">
        {playlists.items.map((item, i) => 
          <Card 
            key={i}  
            style={{ width: '18rem' }} 
            onClick={() => {handleSelect(item)}}>
              <div className=".card-image-link">
                {!_.isEmpty(item.images) ? (
                  <Card.Img 
                    variant="top" 
                    src={item.images[0].url} 
                    alt="" />
                ) : (
                  <img src={defaultLogo} alt="" />
                )}

                <Card.Body>
                  <Card.Title>{item.name}</Card.Title>
                  <Card.Text>
                    <small>By {item.owner.display_name}</small>
                  </Card.Text>
                </Card.Body>
                
              </div>
          </Card>
        )}
      </div>

      {playlists.next !== null && 
        <Button 
          variant="info" 
          type="button" 
          onClick={handleLoadClick}>
            Load More
        </Button>}
    </div>
  );
};
export default PlayList;