import React from 'react';
import { Link } from 'react-router-dom';
import Header from './Header';

const NotFoundPage = () => {
  return (
    <React.Fragment>
      <Header />
      <div>
        Page not found. <Link to="/dashboard">Home Page</Link>
      </div>
    </React.Fragment>
  );
};

export default NotFoundPage;