import React, { useEffect, useState } from 'react';
import _ from 'lodash';
import { useDispatch, useSelector } from 'react-redux';
import { Redirect, useHistory } from 'react-router-dom'
import PlaylistList from './PlaylistList'
import { 
  initiateGetResult, 
  getUserPlaylists, 
  initiateLoadMorePlaylist } from '../actions/result';
import Header from './Header';
import SearchForm from './SearchForm';
import '../styles/Dashboard.css'

const Dashboard = ({setSelectedPlaylist, setExpiryTime, isValidSession}) => {
  const history = useHistory();
  const dispatch = useDispatch();
  
  const [selectedTab, setSelectedTab] = useState('userTab')
  const  { userPlaylist, playlist }  = useSelector(state => state)

  // Get the users playlists
  useEffect(() => {
    async function getPlaylists() {
      await dispatch(getUserPlaylists()).then((res) => {
        /**
         * Clear localStorage and cause redirect 
         * if invalid auth_token is passed
         */ 
        if (res === undefined) {
          localStorage.clear();
          setExpiryTime(0)
        }
      });
    }
    getPlaylists()
  }, [dispatch, setExpiryTime])

  // Get search results and switch to search tab
  const handleSearch = (searchTerm) => {
    dispatch(initiateGetResult(searchTerm)).then((res) => {
      setSelectedTab('searchTab')
    });
  }
  
  //Switching between user/public playlists
  const handleTabChange = (e) => {
    if (e.target.id !== selectedTab) {
      selectedTab === 'userTab' ? 
        setSelectedTab('searchTab') : setSelectedTab('userTab') 
    } else {
      console.log('already selected')
    }
  }

  // Grabbing tracks from a playlist
  async function handlePlaylistSelect(data) {
    // await dispatch(getTracks(data.href))
    setSelectedPlaylist(data)
    history.push(`/playlist/${data.id}/tracks`)
  }

  // Load more playlists
  async function loadMore(playlist, type) {
    await dispatch(initiateLoadMorePlaylist(playlist.next, type));
  }


  return (
    <React.Fragment>
      {isValidSession() ? (
        <div>
        <Header />
        <SearchForm handleSearch={handleSearch}/>
        <div className="Dashboard-tabs">
          <div 
            className={
              `Dashboard-tabs-tab 
              ${selectedTab==="userTab" ? "active" : ""}`
            }
            id='userTab'
            onClick={handleTabChange}>
            My Playlists
          </div>
          <div 
            className={
              `Dashboard-tabs-tab 
              ${selectedTab==="searchTab" ? "active" : ""}`
            } 
            id='searchTab'
            onClick={handleTabChange}>
            Search Results
          </div>
        </div>


        {!_.isEmpty(userPlaylist) && selectedTab === "userTab" && 
          <PlaylistList 
            playlists={userPlaylist} 
            handlePlaylistSelect={handlePlaylistSelect}
            loadMore={loadMore}
            type='user'
          />}


        {!_.isEmpty(playlist) && selectedTab === "searchTab" &&
          <PlaylistList 
            playlists={playlist} 
            handlePlaylistSelect={handlePlaylistSelect}
            loadMore={loadMore} 
            type='public'
          />}
        </div>
      ) : (
        <Redirect
          to={{
            pathname: '/',
            state: {
              session_expired: true
            }
          }}
        />
      )}
    </React.Fragment>
  )
};
export default Dashboard;