import { SET_USER_PLAYLIST, ADD_USER_PLAYLIST } from '../utils/constants';

const userPlaylistReducer = (state = {}, action) => {
  const { userPlaylists } = action;
  switch (action.type) {

    case SET_USER_PLAYLIST:
      return userPlaylists;

    case ADD_USER_PLAYLIST:
      return {
        ...state,
        next: userPlaylists.next,
        items: [...state.items, ...userPlaylists.items]
      };
      
    default:
      return state;
  }
};
export default userPlaylistReducer;