import React, { useEffect, useState } from 'react';
import { Redirect, useParams } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux';
import _ from 'lodash';
import Header from './Header';
import Videoplayer from './Videoplayer'
import youtube from '../utils/youtube'
import '../styles/Tracks.css'
import { Button } from 'react-bootstrap';
import { getTracks } from '../actions/result';


const Tracks = ({isValidSession, selectedPlaylist}) => {
  const dispatch = useDispatch()
  const { playlistId } = useParams()
  const BASE_URL = "https://api.spotify.com/v1/playlists/"

  const [songIndex, setSongIndex] = useState(0)
  const [randomize, setRandomize] = useState(false)
  const [isBusy, setIsBusy] = useState(true)
  const [currTrack, setCurrTrack] = useState({
    id: {
      videoId: "_LLCz1FCWrY"
    },
    snippet: {
      title: "Two time - meme"
    } 
  })
  
  const  { tracks }  = useSelector(state => state.tracks)
  const  { playlistData }  = useSelector(state => state.tracks)


  // if (_.isEmpty(tracks)) {
  //   alert('Something went wrong. Returning to dashboard')
  //   history.push('/dashboard')
  // }

  const query = BASE_URL + playlistId
  useEffect(() => {
    async function getPlaylistData() {
        await dispatch(getTracks(query));
    }


    getPlaylistData().then((res) => {
      // Can now load the main content
      setIsBusy(false)
    });
    
  }, [dispatch, query])
  
  //Search Youtube's API
  async function searchForVideo(trackId) {
    try {
      console.log('youtube request')
      const res = await youtube.get('/search', {
        params: {
          q: `${tracks[trackId].track.name} by ${tracks[trackId].track.artists[0].name}`
        }
      })
      setCurrTrack(res.data.items[0])
      setSongIndex(trackId)
    } catch (e) {
      console.log(e)
    }
  }

  // If song is selected
  const handleSelect = async (e) => {
    let trackId = e.target.id.split('-')[1]
    await searchForVideo(trackId);
  }

  const handleNextTrack = async () => {
    let nextSong
    
    // go to the next index if not random
    // go to the first track if at the last song
    if (!randomize) {
      nextSong = parseInt(songIndex) + 1
      if (nextSong === tracks.length) nextSong = 0;
    } else {
      //Choose a random song if shuffle is on
      nextSong = Math.floor(Math.random() * tracks.length)
    }
    await searchForVideo(nextSong)
  }


  // Toggle Random
  const toggleRandom = () => {
    setRandomize(randomize => !randomize)
  }
  
  // Listing the songs
  const showTracks = () => {
    return tracks.map((item, i) => (
      <div 
        key={i} 
        id={`track-${i}`}
        className="Tracks-track"
        onClick={handleSelect}>{item.track.name} - {item.track.artists[0].name}</div>
    ))
  }
  /**
   * This is needed for now because the 'selectedPlaylist' won't be 
   * assigned if the user types the id of the playlist into the searchbar
   */
  
  if (isValidSession() && !isBusy && _.isEmpty(playlistData)) {
    alert('Something went wrong, returning to Dashboard');
    <Redirect to='/dashboard'/>
  }


  /**
   * Check if the session has not expired 
   * && if react is finished with mounting requests 
   * && if a playlist was found
   * before loading the main content
   */
  return (
    <React.Fragment>
      {isValidSession() && !isBusy && !_.isEmpty(playlistData) ? (
        <div>
          <Header />
          <div>
            <a
              target="_blank"
              href={playlistData.external_urls.spotify}
              rel="noopener noreferrer"
              className="card-image-link"
            >
              <h3 className="Tracks-playlist-title">
                {playlistData.name} 
                <small> By {playlistData.owner.display_name}</small>
              </h3>
            </a>
          </div>

        {/* Load start button or video if playlist has started */}
          {currTrack.etag ? (
            <Videoplayer currTrack={currTrack} videoId={currTrack.id.videoId} handleNextTrack={handleNextTrack}/>
          ) : (
            <div>
            <Button 
              id="startBtn"
              variant="success"
              type="button" 
              onClick={handleNextTrack}
              disabled={_.isEmpty(tracks) ? true : false}>
                Start
            </Button>
            <span> {_.isEmpty(tracks) ? "Playlist is empty" : ""}</span>
            </div>
          )}
          
          <div>
            <Button 
              variant={randomize ? "info" : "secondary"}
              type="button" 
              onClick={toggleRandom}>
                Shuffle: {randomize ? "On" : "Off"}
            </Button>
          </div>
          {!_.isEmpty(tracks) && showTracks()}
        </div>

      ) : (
        isBusy ? (
          <div>Loading</div>
        ) : (
          <Redirect
            to={{
              pathname: '/',
              state: {
                session_expired: true
              }
            }}
          />
        )
      )}
    </React.Fragment>
  );
};
export default Tracks;