import {
  SET_PLAYLIST,
  ADD_PLAYLIST,
  SET_TRACKS,
  SET_USER_PLAYLIST,
  ADD_USER_PLAYLIST
} from '../utils/constants';
import { get } from '../utils/api';

export const setUserPlayList = (userPlaylists) => ({
  type: SET_USER_PLAYLIST,
  userPlaylists
});

export const addUserPlaylist = (userPlaylists) => ({
  type: ADD_USER_PLAYLIST,
  userPlaylists
});

export const setPlayList = (playlists) => ({
  type: SET_PLAYLIST,
  playlists
});

export const addPlaylist = (playlists) => ({
  type: ADD_PLAYLIST,
  playlists
});

export const setTracks = (playlistData, tracks) => ({
  type: SET_TRACKS,
  playlistData,
  tracks
});


export const getUserPlaylists = () => {
  return async (dispatch) => {
    try {
      const API_URL = `https://api.spotify.com/v1/me/playlists`;
      const res = await get(API_URL);
      return dispatch(setUserPlayList(res));
    } catch (error) {
      console.log('error', error);
    }
  };
}

export const initiateGetResult = (searchTerm) => {
  return async (dispatch) => {
    try {
      const API_URL = `https://api.spotify.com/v1/search?query=${encodeURIComponent(
        searchTerm
      )}&type=playlist`;
      const res = await get(API_URL);
      const { playlists } = res;
      return dispatch(setPlayList(playlists));
    } catch (error) {
      console.log('error', error);
    }
  };
};

export const getTracks = (URL) => {
  return async (dispatch) => {
    let allTracks = []
    let playlistData = {}
    let counter = 0;

    async function recursivelyGetSongs(URL) {
      try {
        const res = await get(URL)
        
        // Save the playlist data on the first request
        if (res.owner){
          playlistData = res;
        } 

        // Add to the current tracks
        let tracks
        res.tracks ? tracks = res.tracks : tracks = res
        allTracks.push(...tracks.items)

        // loop until all tracks are received.
        if (tracks.next !== null && counter < 5) {
          //Counter to avoid infinite loop while testing
          counter++
          await recursivelyGetSongs(tracks.next)
        } else {
          return dispatch(setTracks(playlistData, allTracks));
        }
      } catch (error) {
        console.log('error', error);
      }
    } 
    
    await recursivelyGetSongs(URL)
  };
}

export const initiateLoadMorePlaylist = (url, type) => {
  return async (dispatch) => {
    try {
      const res = await get(url);

      //If getting more user playlists
      if (type === 'user') return dispatch(addUserPlaylist(res));
      
      return dispatch(addPlaylist(res.playlists));
    } catch (error) {
      console.log('error', error);
    }
  };
};